import { UMAP } from 'umap-js';
export class UmapExecutor {
    constructor(config) {
        this._canceled = false;
        this._umap = new UMAP(config);
    }
    cancel() {
        this._canceled = true;
    }
    async execute(data, onProgress) {
        return this._umap.fitAsync(data, (epochNumber) => {
            onProgress(epochNumber);
            if (this._canceled) {
                return false;
            }
        });
    }
    get embedding() {
        return this._umap.getEmbedding();
    }
}
